        本隐私协议（以下简称“协议”）由海南积分宝电子商务有限公司（以下简称“本公司”）与使用【乡愁宝小程序】的摊主（以下简称“摊主”）及用户（以下简称“用户”）共同缔结。本公司致力于保护用户和摊主的隐私，在使用本小程序的过程中，本公司将依据国家相关法律法规及本协议规定，以合法、正当、必要的原则处理您的个人信息。

        一、信息收集

        1. 本公司会收集用户和摊主在使用【乡愁宝小程序】时提供的各类个人信息，具体包括但不限于：
          - 基本身份信息：姓名、身份证号、联系方式（如手机号码、电子邮箱）等，这些信息对于用户和摊主的账户注册、身份验证以及服务使用至关重要。
          - 店铺信息（仅针对摊主）：店铺名称、店铺地址、经营范围、营业执照编号等，以便为摊主提供店铺管理和运营服务。
          - 交易信息：包括订单信息（如订单编号、商品信息、交易金额、交易时间等），这是为了完成订单处理、结算及售后服务所需。
          - 支付信息：在用户进行支付操作时，会涉及支付账户信息，但本公司将确保支付信息的处理符合金融监管机构的要求，仅在完成支付流程中使用，且会通过安全加密机制保护其安全性。
          - 设备信息：在您使用本小程序时，会自动收集您的设备相关信息，例如设备型号、操作系统版本、唯一设备标识符（如 IMEI 号、MAC 地址等），主要用于确保服务的兼容性、维护服务的安全性以及优化用户体验。这些信息将以匿名化和去标识化的方式进行处理，不会直接关联到您的个人身份，除非法律另有规定。


        二、信息使用

        1. 本公司将使用收集到的个人信息为您提供和优化服务，具体如下：
          - 提供服务：使用您的信息来创建和管理您的账户，处理订单，为摊主管理店铺、提供商品展示和销售服务，为用户提供商品选购、订单追踪及客户服务等。
          - 业务运营：进行数据分析，包括但不限于统计分析用户和摊主的行为模式、偏好，以改进我们的服务，如调整菜品推荐算法、优化服务流程和功能，提升服务质量。
          - 沟通与服务：使用您的信息向您发送与服务相关的通知，如订单状态更新、发货通知、服务维护通知等，以确保您能及时了解服务进展。同时，我们可能会根据您的偏好和历史行为向您推送个性化的推荐信息，如您可能感兴趣的菜品、促销活动或优惠信息。若您不希望接收营销信息，可以通过【乡愁宝小程序】内的设置功能随时选择退订，我们将尊重您的选择。
          - 安全与防欺诈：利用收集到的信息来维护服务的安全性，预防和检测可能的欺诈、安全漏洞及滥用行为，保障用户和摊主的权益。


        三、信息共享

        1. 未经您的明确同意，本公司不会将您的个人信息向第三方共享，以下情况除外：
          - 服务提供商：为了向您提供更优质的服务，我们可能会与部分服务提供商合作，如支付机构、物流配送公司、技术支持服务商等。在共享信息前，我们会与这些服务提供商签订严格的保密协议，确保其仅在为您提供服务的必要范围内使用您的信息，并采取适当的安全和保密措施。例如，支付机构仅会在处理支付事务时使用您的支付信息，且仅能访问其所需的最少信息；物流配送公司仅能获取发货所需的收货地址等信息。
          - 法律要求：当法律、法规、司法或行政命令要求我们披露信息时，我们将依法配合，同时会在合理范围内保障您的权益。
          - 合并与收购：在公司合并、收购、重组或出售部分业务时，您的信息可能会被转移至新的实体，但我们会确保新的实体继续履行本协议所规定的隐私保护义务。


        四、信息存储

        1. 本公司将根据国家法律法规的要求，以安全的方式存储您的个人信息，确保信息的保密性和完整性。存储时间将根据信息的使用目的和相关法律规定而定，对于不再需要的信息，我们将及时进行删除或匿名化处理。
        2. 我们采用多种安全措施来保护信息存储的安全，包括但不限于：
          - 采用加密技术对存储的信息进行加密，防止未经授权的访问。
          - 实施访问控制措施，仅允许授权人员在必要时访问信息。
          - 定期进行数据备份和恢复测试，以防止数据丢失或损坏。


        五、信息访问与修改

        1. 您有权随时访问、更新和修改您在【乡愁宝小程序】中的个人信息。您可以通过【乡愁宝小程序】内的用户中心或摊主管理界面进行操作，或者联系我们的客户服务团队。我们将及时响应您的请求，并确保信息的更新准确反映您的最新情况。
        2. 若您发现您的信息有误或不完整，您有权要求我们进行更正或补充。在某些情况下，您还可以请求删除您的部分或全部个人信息，但需注意，在删除信息可能影响服务提供或违反法律规定的情况下，我们可能会在合理范围内保留必要信息，并向您说明原因。


        六、信息保护

        1. 本公司高度重视信息保护，采取多维度的安全措施来防止未经授权的访问、使用、披露、修改或破坏您的个人信息，这些措施包括但不限于：
          - 技术防护：运用先进的安全技术和工具，如防火墙、加密技术、安全套接字层（SSL）协议等，确保信息在传输和存储过程中的安全。
          - 人员管理：对员工进行信息安全培训，确保员工遵守严格的信息处理和保护规定，只有经授权的员工在必要时才能接触和处理用户信息。
          - 安全审计：定期进行安全审计和风险评估，及时发现并解决潜在的安全风险。
        2. 若发生信息安全事件，我们将按照法律法规的要求，在不影响调查和处理的前提下，尽快通知受影响的用户和摊主，并采取相应的补救措施，包括但不限于提供风险提示、协助您采取防范措施等。


        七、第三方链接和服务

        1. 【乡愁宝小程序】可能包含指向第三方网站或服务的链接，这些第三方网站或服务有其独立的隐私政策和信息处理方式，本公司对其隐私政策和信息处理行为不承担责任。
        2. 在使用第三方服务时，请您务必仔细阅读并理解其隐私政策，谨慎操作。当您点击这些链接时，即表示您将离开本公司的服务环境，本公司无法控制第三方对您信息的收集和处理，您需自行判断风险。


        八、儿童隐私

        1. 本小程序不针对儿童（不满 14 周岁）提供服务。我们致力于遵守相关儿童隐私保护法律法规，若发现我们收集了儿童的信息，将采取合理措施尽快删除相关信息。若您是儿童的家长或监护人，请及时联系我们，我们将协助您进行信息删除和保护操作。


        九、隐私协议的变更

        1. 本公司会根据业务发展、法律法规的更新或监管要求，适时更新本隐私协议。我们将通过【乡愁宝小程序】内的公告系统、电子邮件或短信等合理的通讯方式向您通知协议的变更内容。
        2. 您在收到隐私协议更新通知后继续使用【乡愁宝小程序】，将视为您同意更新后的隐私协议；若您不同意更新内容，您有权停止使用本小程序。


        十、法律适用和争议解决

        1. 本隐私协议的解释、效力及争议解决均适用中华人民共和国法律。
        2. 在协议履行过程中，若双方因本隐私协议的内容或履行事宜发生争议，应首先尝试通过友好协商解决；若协商不成，任何一方可向本公司所在地人民法院提起诉讼。


